.App {
	height: 1000vh;
	text-align: center;
	-ms-overflow-style: none;
	scrollbar-width: none;
}
.App::-webkit-scrollbar {
	display: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

